import {Component, OnInit} from '@angular/core';
import {APIService} from '../../../globals/services/API.service';
import {hunkDate} from '../../../globals/pipes/hunkDate.pipe';

@Component({
    selector: 'ingatlan-licit',
    moduleId: module.id,
    templateUrl: 'ingatlanlicit.html',
    providers:[APIService],
    styleUrls: [""],
    inputs: ['licits'],
    directives: [],
    pipes:[hunkDate]


})
export class IngatlanLicitComponent implements OnInit{
    licits:number;
   licitalasok:Array<any>;
    constructor(
        private _APIService:APIService
        ) {

    }
    ngOnInit(){
        this.load();
    }
    load(){
        this._APIService.getResponseGET('api/ingatlan/'+this.licits+'/licits/', '').subscribe(
                data=> {
                console.log(data);
                    this.licitalasok=data;
            },
                error=> {
                console.log(error);
            })



    }
}
